/* ImageGallery.module.css */
.gallery {
    list-style: none;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    margin: 0;
    padding: 20px;
    gap: 20px;
    background-color: #f0f8ff;
    min-height: 100vh;
}

.galleryItem {
    border-radius: 10px;
    overflow: hidden;
    transition: transform 0.3s ease;
}

.galleryItem img {
    display: block;
    width: 100%;
    height: auto;
    transition: transform 0.3s ease;
}

.galleryItem:hover {
    transform: translateY(-5px);
}

.galleryItem:hover img {
    transform: scale(1.05);
    border: 3px solid #ff85a2;
}